import React from "react";
import styled from 'styled-components';
import homeBackground from "../images/logo.jpg";
import DefaultLayout from "../components/layout/DefaultLayout";
import HomeBackground from "../components/common/HomeBackground";
import MaskDark from "../components/common/MaskDark";
import Title from "../components/common/Title";
import FontFamily from "../components/common/FontFamily";
import Middle from "../components/common/Middle";

const ContentSize = styled.div`
	width: 100%;
`;
const OneHalf = styled.div`
    margin: 10px 185px 0px 185px;
    width: 75%;
    display: flex;
`;
const Column = styled.div`
    width: 50%;
    height: 100%;
    margin: 10px;
    padding: 10px;
    background-color: #F5F5F5;
    border-radius: 10px;
`;

const ExperiencePage = () => {
    return (
        <DefaultLayout>
            <HomeBackground url={homeBackground}>
                <MaskDark>
                    <Middle>Experience</Middle>
                </MaskDark>
            </HomeBackground>
            <ContentSize>
                <FontFamily>
                    <Title>大學一年級</Title>
                    <OneHalf>
                        <Column>上學期 第一次學習程式語言(C++)</Column>
                        <Column>下學期 學習各種程式語言(Java、Python、Html、CSS、Java、JavaScript)及物件導向</Column>
                    </OneHalf>
                    <Title>大學二年級</Title>
                    <OneHalf>
                        <Column>上學期 參加參訪</Column>
                        <Column>下學期 接觸教學助理(物件導向TA)</Column>
                    </OneHalf>
                    <Title>大學三年級</Title>
                    <OneHalf>
                        <Column>上學期 教學助理(資料結構TA)、開始製作畢業專題、學習React</Column>
                        <Column>下學期 教學助理(資料庫TA)、製作畢業專題中</Column>
                    </OneHalf>
                    <Title>大學四年級</Title>
                    <OneHalf>
                        <Column>上學期 繼續當教學助理(資料結構TA、教導下一屆學弟妹如何當好的TA)、畢業專題中發表</Column>
                        <Column>下學期 繼續當教學助理(資料庫TA、教導下一屆學弟妹如何當好的TA)、企業實習</Column>
                    </OneHalf>
                </FontFamily>
            </ContentSize>
        </DefaultLayout>
    )
};

export default ExperiencePage;