import React from 'react';
import styled from 'styled-components';
import Container from '../common/Container';

const FooterContainer = styled.footer`
	display: block;
	width: 99.8%;
	height: 50px;
	color: #FFFFFF;
	padding-top: 32px;
	margin-top: 30px;
	background: #000000;
`;

const Footer = () => {
	return (
		<FooterContainer>
			<Container>@ LU YI CHEN</Container>
		</FooterContainer>
	);
};

export default Footer;