import React from "react";
import styled from 'styled-components';
import DefaultLayout from "../components/layout/DefaultLayout";
import HomeBackground from "../components/common/HomeBackground";
import MaskDark from "../components/common/MaskDark";
import Title from "../components/common/Title";
import FontFamily from "../components/common/FontFamily";
import Content from "../components/common/Content";
import Middle from "../components/common/Middle";
import Title2 from "../components/common/Title2";
import Title2Content from "../components/common/Title2Content";

import homeBackground from "../images/logo.jpg";
import reactProject from "../images/reactProject.jpg";

import Video from "https://youtu.be/tl8ApwD0ud0";

const ContentSize = styled.div`
	width: 100%;
`;
const Title2ContentImg = styled.div`
    width: 94.5%;
    height: 600px;
    margin-top: 16px;
    margin-left: 50px;
    background-image: ${(props) => `url(${props.url})`};
    background-position: center;
    background-size: cover;
`;
const Title2ContentVideo = styled.video`
    margin-top: 16px;
    margin-left: 180px;
    width: 100%;
    max-width: 600px;
    border: 1px solid #ccc;
    src: ${(props) => `url(${props.url})`};
`;

const GalleryPage = () => {
    return (
        <DefaultLayout>
            <HomeBackground url={homeBackground}>
                <MaskDark>
                    <Middle>Gallery</Middle>
                </MaskDark>
            </HomeBackground>
            <ContentSize>
                <FontFamily>
                    <Title>大學一年級</Title>
                    <Content>
                        <Title2>卡牌遊戲(nyonyo)</Title2>
                        <Title2Content>以物件導向實作卡牌遊戲(nyonyo)</Title2Content>
                        <Title2ContentImg url={homeBackground} />
                        <Title2ContentVideo controls src={Video} />
                    </Content>
                    <Title>大學二年級</Title>
                    <Content>
                        <Title2>簡易資料庫</Title2>
                        <Title2Content>以python實作簡易資料庫</Title2Content>
                        <Title2ContentImg url={homeBackground} />
                        <Title2ContentVideo controls src={Video} />
                    </Content>
                    <Title>大學三年級</Title>
                    <Content>
                        <Title2>APP畢業專題</Title2>
                        <Title2Content>寫有關畢業專題的東西</Title2Content>
                        <Title2ContentImg url={homeBackground} />
                        <Title2ContentVideo controls src={Video} />
                        <Title2>遊戲1A2B注音版</Title2>
                        <Title2Content>以python實作遊戲1A2B注音版</Title2Content>
                        <Title2ContentImg url={homeBackground} />
                        <Title2ContentVideo controls src={Video} />
                    </Content>
                    <Title>大學四年級</Title>
                    <Content>
                        <Title2>zhenの簡歷</Title2>
                        <Title2Content>使用React 製作現在的網頁</Title2Content>
                        <Title2ContentImg url={reactProject} />
                    </Content>
                </FontFamily>
            </ContentSize>
        </DefaultLayout>
    )
};

export default GalleryPage;