import styled from 'styled-components';
import React from 'react';
import Container from '../common/Container';
import { Link } from "react-router-dom";
import headshot from "../../images/headshot.jpg";

const HeaderContainer = styled.div`
	display: block;
	width: 300px;
	height: 100%;
    background: #E5C8C7;
`;
const HeaderContainerUp = styled.div`
    display: block;
    width: 100%;
    height: 300px;
    margin-top: 20px;
    margin-bottom: 100px;
`;
const HeaderContainerName = styled.div`
    padding: 20px 0px 10px 0px;
    font-size: 22px;
`;
const HeaderContainerSchool = styled.div`
    margin-top: 10px;
    font-size: 16px;
`;
const HeaderContainerSchoolContent = styled.div`
    font-size: 16px;
`;
const HeaderContainerDown = styled.div`
    display: block;
    width: 100%;
    height: 300px;
    padding-top: 80px;
    a {
        margin-bottom: 4px;
        color: #000000;
        text-decoration: none;
        font-size: 18px;
        padding: 4px 4px 4px 0px;
    }
`;
const Image = styled.div`
    width: 100%;
    height: ${(props) => props.height}px;
    background-image: ${(props) => `url(${props.url})`};
    background-position: center;
    background-size: cover;
    border-radius: 50%; /* 設置圓角為 50% 以使圖像變成圓形 */
    overflow: hidden; /* 隱藏圖像的溢出部分 */
`;
const Underline = styled.div`
    margin: 10px 20px;
    position: relative;
    display: inline-block;

    &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 0;
        height: 2px;
        background-color: #92685d;
        transition: width 0.3s ease; /* 添加過渡效果 */
    }

    &:hover:after {
        width: 100%; /* 鼠標懸停時下劃線出現 */
    }
`;

const Header = () => {
    return (
        <HeaderContainer>
            <HeaderContainerUp>
                <Container>
                    <Link to="/"><Image height={200} url={headshot} /></Link>
                    <HeaderContainerName>Lu Yi Chen</HeaderContainerName>
                    <HeaderContainerSchool>Xiufeng Senior High School</HeaderContainerSchool>
                    <HeaderContainerSchoolContent>2017~2020</HeaderContainerSchoolContent>
                    <HeaderContainerSchool>Chinese Culture University CSIE</HeaderContainerSchool>
                    <HeaderContainerSchoolContent>2020~2024</HeaderContainerSchoolContent>
                </Container>
            </HeaderContainerUp>
            <HeaderContainerDown>
                <Container>
                    <Link to="/Home"><Underline>Home</Underline></Link>
                    <Link to="/Experience"><Underline>Experience</Underline></Link>
                    <Link to="/Gallery"><Underline>Gallery</Underline></Link>
                    <Link to="/AboutMe"><Underline>About Me</Underline></Link>
                </Container>
            </HeaderContainerDown>
        </HeaderContainer>
    );
};

export default Header;