import React from "react";
import styled from 'styled-components';
import homeBackground from "../images/logo.jpg";
import igLogo from "../images/IgLogo.jpg";
import gmailLogo from "../images/GmailLogo.png";
import githubLogo from "../images/GitHubLogo.png";
import DefaultLayout from "../components/layout/DefaultLayout";
import HomeBackground from "../components/common/HomeBackground";
import MaskDark from "../components/common/MaskDark";
import { Link } from "react-router-dom";
import Middle from "../components/common/Middle";

const OneQuarter = styled.div`
    margin: 350px 185px 350px 185px;
    width: 70%;
    height: 100px;
    display: flex;
`;
const Connection = styled.div`
    width: 33.33%;
`;
const ConnectionImg = styled.div`
    width: 100%;
    height: 100%;
    background-image: ${(props) => `url(${props.url})`};
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
`;

const AboutMePage = () => {
    return (
        <DefaultLayout>
            <HomeBackground url={homeBackground}>
                <MaskDark>
                    <Middle>
                        About Me
                    </Middle>
                </MaskDark>
            </HomeBackground>
            <OneQuarter>
                <Connection>
                    <Link to="https://www.instagram.com/margaret0619?igsh=MWdlOWxpbGU4dGJmZQ=="><ConnectionImg url={igLogo} /></Link>
                </Connection>
                <Connection>
                    <Link to="/margaret220104@gmail.com"><ConnectionImg url={gmailLogo} /></Link>
                </Connection>
                <Connection>
                    <Link to="https://github.com/Margaret910302"><ConnectionImg url={githubLogo} /></Link>
                </Connection>
            </OneQuarter>
        </DefaultLayout>
    )
};

export default AboutMePage;