import React from "react";
import { Image } from "antd";
import styled from 'styled-components';
import DefaultLayout from "../components/layout/DefaultLayout";
import FontFamily from "../components/common/FontFamily";
import HomeBackground from "../components/common/HomeBackground";
import MaskDark from "../components/common/MaskDark";
import Title from "../components/common/Title";
import Content from "../components/common/Content";
import Middle from "../components/common/Middle";
import Title2 from "../components/common/Title2";
import ImageView from "../components/common/ImageView";

import homeBackground from "../images/logo.jpg";
import htmlLogo from "../images/htmlLogo.jpg";
import cssLogo from "../images/cssLogo.jpg";
import javascriptLogo from "../images/javascriptLogo.jpg";
import cLogo from "../images/c++Logo.png";
import pythonLogo from "../images/pythonLogo.png";
import javaLogo from "../images/javaLogo.png";
import sqlLogo from "../images/sqlLogo.png";
import reactLogo from "../images/reactLogo.png";
import springbootLogo from "../images/springbootLogo.png";
import mysqlLogo from "../images/mysqlLogo.png";
import pccuTranscript1 from "../images/pccuTranscript1.jpg";
import pccuTranscript2 from "../images/pccuTranscript2.jpg";
import oopTa from "../images/1102_OOP_TA.jpg";
import dataStructureTa from "../images/1111_dataStructure_TA.jpg";
import sqlTa from "../images/1112_SQL_TA.jpg";

const ContentSize = styled.div`
	width: 100%;
`;

const HomePage = () => {
    const images = [pccuTranscript1, pccuTranscript2, oopTa, dataStructureTa, sqlTa,];
    return (
        <DefaultLayout>
            <HomeBackground url={homeBackground}>
                <MaskDark>
                    <Middle>Home</Middle>
                </MaskDark>
            </HomeBackground>
            <ContentSize>
                <FontFamily>
                    <Title>自我介紹</Title>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;嗨，我是來自一個六口之家的大學生，家中成員有爸爸、媽媽、妹妹、爺爺、奶奶和我。爸爸是一名貨運司機；媽媽則是一位創業中的早餐店老闆娘；妹妹目前在海洋大學就讀一年級；爺爺奶奶則在環保產業中從事資源回收和再利用的工作。</Content>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;高中三年，我就讀於秀峰高中的普通班，對電資領域毫無概念。然而老師們認為我具有不錯的邏輯能力，因此推薦我進入資工系。雖然起初對理論知識及實作感到陌生，但通過不斷努力學習，我逐漸提高了大學的專業成績。</Content>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;在這大學四年中，我深刻體會到了資工領域的廣度和深度。透過努力學習，我不僅獲得了更多技術技能，還找到了自己的興趣所在。我希望能將這些實作技能應用於實際工作中，並不斷追求更深層次的專業知識。</Content>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;我認為「不虎頭蛇尾，堅持到底」的學習態度是最好的讀書方式。我相信在學習中持續追求理解和突破，堅持不懈地解決問題，將是我未來學術和專業發展的動力。這種態度也使我能夠從挫折和困難中獲得成長，成為一位不斷進步的學習者。</Content>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;在大學期間，我從電資領域的陌生開始，逐漸轉變為對程式語言的熟練、對專業知識的深入理解，並運用這些知識參與實際的專題研究。我也有實習經驗，在實習期間負責車牌標註和安裝應用程式的工作，這讓我學到了與團隊合作和解決問題的能力。</Content>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;此外，我還在大學中擔任TA，包括物件導向、資料結構和資料庫等課程。這些經歷讓我得以深入了解和分享我所學的知識。</Content>
                    <Title>專業背景</Title>
                    <Content>
                        <Title2>必修</Title2>
                        <Content>程式設計、物件導向程式設計、資料結構、資料庫、線性代數、離散數學、電腦網路</Content>
                        <Title2>選修</Title2>
                        <Content>演算法、Python程式設計、電子電路、多媒體系統、多媒體數位剪輯、網路視窗程式設計、遊戲程式設計、圖像辨識、3D電腦動畫設計、影像處理、智慧型手機程式設計、資訊安全、網際網路程式設計、LINUX系統、單晶片系統實作
                        </Content>
                    </Content>
                    <Title>技能和專長</Title>
                    <Content>
                        <Title2>程式語言<br/></Title2>
                            &nbsp;&nbsp;&nbsp;&nbsp;
                            <Image src={htmlLogo} alt="HTML Logo" style={{ width: '70px', height: 'auto' }} />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <Image src={cssLogo} alt="CSS Logo" style={{ width: '70px', height: 'auto' }} />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <Image src={javascriptLogo} alt="JavaScript Logo" style={{ width: '65px', height: 'auto' }} />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <Image src={cLogo} alt="C++ Logo" style={{ width: '70px', height: 'auto' }} />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <Image src={pythonLogo} alt="Python Logo" style={{ width: '80px', height: 'auto' }} />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <Image src={javaLogo} alt="Java Logo" style={{ width: '80px', height: 'auto' }} />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <Image src={sqlLogo} alt="MySQL Logo" style={{ width: '65px', height: 'auto' }} />
                        <Title2>框架<br/></Title2>
                            &nbsp;&nbsp;&nbsp;&nbsp;
                            <Image src={reactLogo} alt="React Logo" style={{ width: '70px', height: 'auto' }} />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <Image src={springbootLogo} alt="SpringBoot Logo" style={{ width: '70px', height: 'auto' }} />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <Image src={mysqlLogo} alt="MySQL Workbench Logo" style={{ width: '100px', height: 'auto' }} />
                    </Content>
                    <Title>興趣和愛好</Title>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;音樂遊戲是我生活中不可或缺的一部分，當我沉浸在音樂的節奏中，通過手指的節奏和眼睛的敏銳度，我感受到一種前所未有的快感和成就感。無論是節奏遊戲還是音樂合成遊戲，都讓我感受到音樂和遊戲的奇妙結合，讓我彷彿置身於一個充滿音樂和節奏的世界。</Content>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;還有另一個我喜歡的活動是密室逃脫，在這種遊戲中，我和我的朋友們需要合作解決一系列謎題和挑戰，逃脫一各個虛構密室。這種活動不僅考驗了智力和創造力，還增強朋友之間的團隊合作和溝通能力，無論有沒有逃脫成功都讓我們感到極大的滿足和成就感。</Content>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;除了遊戲類，我也很喜歡品嚐各種美食，無論是品嚐異國風味還是享受家鄉美食，只要一吃到美味的食物心情便能感到愉悅和滿足。每一次的美食之旅都是一次對味蕾的探索和享受，讓我更加熱愛生活的美好。</Content>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;此外我還喜歡旅行，特別是到日本旅遊，日本不僅擁有豐富的文化遺產和美麗的自然風景，還有各種各樣的美食和娛樂活動。在日本的旅行中，我能夠感受到不同的文化氛圍，體驗到不同的生活方式，這讓我對世界充滿了好奇心和探索的慾望。</Content>
                    <Content>&nbsp;&nbsp;&nbsp;&nbsp;最後賺錢也是我生活中重要的一部分，通過努力工作和理財，我能夠實現自己的夢想和目標，並享受到物質和精神上的豐富。賺錢不僅給我帶來了經濟上的穩定和自由，還讓我更加堅定地走向自己的人生道路。</Content>
                    <Title>成績與獎狀</Title>
                    <Content>
                        <ImageView images={ images } />
                    </Content>
                </FontFamily>
            </ContentSize>
        </DefaultLayout>
    )
};

export default HomePage;